import React, { useEffect, useRef, useState } from 'react';
import { db } from '../../utils/firebase'
import Spinner from 'react-spinners/ScaleLoader'
import './index.css'
import syncingIcon from '../../assets/synced.png'
import syncIcon from '../../assets/sync.png'
import clipboard from '../../assets/clipboard.png'
import clear from '../../assets/clear.png'
import {data} from '../../data/data.json'

const TS = {
	'*': '<b>',
	'_': '<i>',
}
const TE = {
	'*': '</b>',
	'_': '</i>',
}

const commonstart = /(((?<![a-zA-Z0-9])\*(?=\S)).+?(?<=\S)\*(?![a-zA-Z0-9]))|(((?<![a-zA-Z0-9])_(?=\S)).+?(?<=\S)_(?![a-zA-Z0-9]))/
const regexStart = {
	"*": /(((?<![a-zA-Z0-9])\*(?=\S)).+?(?<=\S)\*(?![a-zA-Z0-9]))/,
	"_": /(((?<![a-zA-Z0-9])_(?=\S)).+?(?<=\S)_(?![a-zA-Z0-9]))/
}
const regexEnd = {
	"*": /((?<=(?<=((?<![a-zA-Z0-9])\*(?=\S)).*?)\S)\*(?![a-zA-Z0-9]))/,
	"_": /((?<=(?<=((?<![a-zA-Z0-9])_(?=\S)).*?)\S)_(?![a-zA-Z0-9]))/,
}
const invert = {
	"_": "*",
	"*": "_"
}

function replace (str, start, end, type) {
	let arr = str.split('')
	arr.splice(start, 1, TS[type])
	arr.splice(end, 1, TE[type])
	return arr.join('');
}



function format(initstr, inittype){
	initstr += " "
	let start, end=-1, final='', left='';

	left = initstr;
	do {
		start = left.search(inittype?regexStart[invert[inittype]]:commonstart)
		if(start===-1) {
			final = final+left;
			break;
		}
		let type = left[start]
		end = left.search(regexEnd[type])

		let str = left.substring(0, end)
		if(end<left.length-1) left = left.substring(end+1)

		let cache = replace(str, start, end, type)
		if(!inittype) cache = format(cache, type);
		final += cache;
	} while(left)

	return final.substring(0, final.length-1);
}

const getCommand = (path, title, text) => {
	let x = path.split("-")
	let nData = JSON.parse(JSON.stringify(data));
	let crnt = nData;
	let y = x.splice(x.length-1, 1)
	for(let i of x){
		if(i){
			console.log(Object.keys(crnt.children), i)
			// if(crnt.children) crnt.children = {};
			crnt = crnt.children[i];
		}
	}
	if(!crnt.children) crnt.children = {};
	if(!crnt.children[y[0]]) crnt.children[y[0]] = {};
	crnt.children[y[0]].path = path+".js";
	crnt.children[y[0]].title = title;
	return `sh adddata "${path}" ${JSON.stringify(JSON.stringify({data: nData}).replace(/\\/g, '\\\\'))} "${text.replace(/\"/g, "\\\"")}"`;
}
const urlregex = /(http|ftp|https):\/\/([\w_-]+(?:(?:\.[\w_-]+)+))([\w.,@?^=%&:/~+#-]*[\w@?^=%&/~+#-])?/

const Editor = props => {
	const [loading, setLoading] = useState(true)
	const loadingRef = useRef(true);
	const [inputText, setInputText] = useState('');
	const [converted, setConverted] = useState('')
	const [command, setCommand] = useState('')
	const [fileName, setFileName] = useState('')
	const [title, setTitle] = useState('')
	const syncingRef = useRef(false);
	const [syncing, setSyncing] = useState(false);
	const txtEdit = useRef(null)
	const txtConvert = useRef(null)
	const txtCmd = useRef(null)
	
	const update = (txt) => {
		setInputText(txt);
		let lines = txt.split('\n');
		for(let i in lines){
			const line = lines[i];
			let words = line.split(' ');
			for(let j in words){
				const word = words[j];
				if(word.match(urlregex)){
					words[j] = `<a href="${word}">${word}</a>`;
				}
			}
			lines[i] = words.join(' ');
		}
		for(let i in lines) {
			lines[i] = format(lines[i]);
		}
		let final = lines.join('<br>')
		final = final.replace(/"/g, "\\\"")
		setConverted(final)
		db.ref('editor').set(final)
		// if(props.addTo || props.editAt) {
		// 	setCommand(getCommand(props.addTo || props.editAt, final))
		// }
	}
	useEffect(() => {
		console.log(props.editAt)
		if(!props.editAt) {
			db.ref('editor').on('value', snap => {
				if(syncingRef.current || loadingRef.current) {
					setConverted(snap.val())
					if(loading) {
						setLoading(false);
						loadingRef.current = false;
					}
				}
			})
		} else {
			let branch = data;
			if(props.editAt!=='index'){
				let pArr = props.editAt.split('-');
				for(let i of pArr) {
					if(i)
						branch = branch.children[i];
				}
			}
			if(branch.path)
				import('../../data/'+branch.path).then(a => loadContent(a.default, branch.title))
			else
				loadContent(branch.content, branch.title);
		}
	}, [props.editAt, props.addTo])
	const loadContent = (cnt='', tle='') => {
		cnt = cnt.replace(/<\s*(\/)?\s*b\s*>/g, '*').replace(/<\s*(\/)?\s*br\s*>/g, '\n').replace(/<\s*(\/)?\s*i\s*>/g, '_').replace(/\\\"/g, "\"")
		let indexOfLink = cnt.search(/<a\s*href="\S*?">.+?<\/a>/g);
		while(indexOfLink>-1) {
			cnt = cnt.replace(/<a\s*href="\S*">/, "")
			cnt = cnt.replace(/<\/a>/, "")
			indexOfLink = cnt.search(/<a\s*href="\S*?">.+?<\/a>/g);
		}
		setInputText(cnt);
		setTitle(tle)
		update(cnt);
		setLoading(false)
	}
	const copy = () => {
		// if (typeof(navigator.clipboard)=='undefined') {
			txtConvert.current.select();
			document.execCommand('copy')
			if(!syncing) txtEdit.current.focus()
			return;
		// }
		// navigator.clipboard.writeText(converted).then(function() {
		// 		alert(`successful!`);         
		// }, function(err) {
		// 		alert('unsuccessful!', err);
		// });
	}
	const copyCmd = () => {
		// if (typeof(navigator.clipboard)=='undefined') {
			txtCmd.current.select();
			document.execCommand('copy')
			if(!syncing) txtEdit.current.focus()
			alert('coppppy')
			return;
		// }
		// navigator.clipboard.writeText(converted).then(function() {
		// 		alert(`successful!`);         
		// }, function(err) {
		// 		alert('unsuccessful!', err);
		// });
	}
	return (
		<div className="editor">
			{loading?
				<div style={{flex: 1, display: 'flex', alignItems: 'center', justifyContent: 'center'}}>
				<Spinner />
				</div>
			:
				<>
					<div className="editor-inner">
						{syncing || <textarea value={inputText} ref={txtEdit} className="editor-item" onChange={e => update(e.target.value)} />}
						<textarea ref={txtConvert} className="editor-item" value={converted} readOnly />
						<div className="editor-tool-box">
							{syncing || <button onClick={() => update('')} className="editor-tool-wrapper" style={{backgroundImage: 'url('+clear+')'}} />}
							<button onClick={copy} className="editor-tool-wrapper" style={{backgroundImage: 'url('+clipboard+')'}} />
							<button onClick={() => {
								setSyncing(a => !a)
								syncingRef.current = !syncingRef.current;
							}} className="editor-tool-wrapper" style={{backgroundImage: 'url("'+(syncing?syncingIcon:syncIcon)+'")', borderColor: syncing?'#008a00':'#290000'}}></button>
						</div>
					</div>
					{(props.editAt || props.addTo) && (
						<div className="editor-footer">
							{props.addTo && <input placeholder="Enter file name" className="editor-footer-input" onChange={a => setFileName(a.target.value)} />}
							<input placeholder="Enter title" onChange={a => setTitle(a.target.value)} className="editor-footer-input" value={title} />
							<button disabled={!((props.editAt || fileName) && title)} onClick={() => setCommand(getCommand(props.addTo? props.addTo+"-"+fileName : props.editAt, title, converted))} className="editor-tool-wrapper" style={{margin: '0em 0em 1em 0em', backgroundColor: "#00f5"}} />
							<input ref={txtCmd} value={command} className="editor-footer-input" readOnly />
							<button onClick={() => copyCmd()} className="editor-tool-wrapper" style={{margin: '0em 1em 1em 0em', backgroundImage: 'url('+clipboard+')'}} />
						</div>
					)}
				</>
			}
		</div>
	)
}

export default Editor